<template>
  <el-row>
    <el-col :span="24"><img src="./assets/logo.png" height="96" /></el-col>
  </el-row>
  <el-row>
    <el-col :span="6">
      <el-row>
        <el-form ref="form" :model="blob" label-position="left">
          <el-form-item label="Number of samples">
            <el-input v-model="blob.nSamples" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="Number of features">
            <el-input v-model="blob.nFeatures" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="Number of centers">
            <el-input-number
              v-model="blob.nCenters"
              :min="1"
              size="mini"
              @change="changeNumberCenters"
            ></el-input-number>
          </el-form-item>

          <el-form-item label="Std of cluster" size="mini" label-position="top">
            <el-col :span="8">
              <el-input
                v-model="blob.clusterStd"
                :disabled="!blob.globalStd"
              ></el-input>
            </el-col>

            <el-col :span="10" :push="2">
              <el-switch
                v-model="blob.globalStd"
                active-text="Global"
                inactive-text="Individual"
              ></el-switch>
            </el-col>
          </el-form-item>

          <el-row v-if="!blob.globalStd">
            <el-form-item
              v-for="index in parseInt(blob.nCenters)"
              :key="index"
              :label="'Cluster ' + index + ' std'"
              size="mini"
            >
              <el-input v-model="blob.individualStd[index]"></el-input>
            </el-form-item>
          </el-row>
          <el-button @click="generateData">Generate data</el-button>
        </el-form>
      </el-row>
    </el-col>
    <el-col :span="16" :push="1">
      <apexchart
        v-if="data"
        width="100%"
        type="scatter"
        :options="options"
        :series="data"
      ></apexchart>
    </el-col>
  </el-row>
</template>

<script lang="ts">
import { defineComponent, Ref, ref } from "vue";
import jStat from "jstat";
import _ from "lodash";
import { ChartOptions } from "./plots";
import MultivariateNormal from "multivariate-normal";
import { Matrix } from "ml-matrix";

export default defineComponent({
  name: "App",
  components: {},
  data() {
    return {};
  },
  setup() {
    const center_box = [-10, 10];
    const blob = ref({
      nSamples: 100,
      nFeatures: 2,
      nCenters: 3,
      globalStd: true,
      clusterStd: 1.0,
      individualStd: [] as number[],
    });
    blob.value.individualStd = _.range(blob.value.nCenters + 1).map(() => 1.0);
    const options: ChartOptions = {
      title: {
        text: "Data",
      },
      chart: {
        type: "scatter",
        animations: {
          enabled: false,
        },
        toolbar: {
          show: false,
        },
        zoom: {
          enabled: false,
        },
      },
      yaxis: {
        labels: {
          formatter: (val: number) => `${val.toFixed(2)}`,
        },
      },
    };
    const changeNumberCenters = function (value: number) {
      console.log(value);
      const n = blob.value.individualStd.length;
      if (value < n) {
        blob.value.individualStd = _.take(blob.value.individualStd, value);
      } else if (value > n) {
        const diff = value - n;
        blob.value.individualStd = _.concat(
          blob.value.individualStd,
          _.range(diff).map(() => 1.0)
        );
      }
    };
    const data: Ref<any> = ref(null);
    const generateData = function () {
      const settings = blob.value;
      const centers = _.range(settings.nCenters).map(() => {
        return _.range(settings.nFeatures).map(() =>
          jStat().uniform(center_box[0], center_box[1]).sample()
        );
      });

      let stdMatrices: Matrix[];

      if (blob.value.globalStd) {
        stdMatrices = _.range(settings.nCenters).map(() =>
          Matrix.identity(
            settings.nFeatures,
            settings.nFeatures,
            settings.clusterStd
          )
        );
      } else {
        stdMatrices = _.range(settings.nCenters).map((i) =>
          Matrix.identity(
            settings.nFeatures,
            settings.nFeatures,
            settings.individualStd[i]
          )
        );
      }

      console.log(_.range(settings.nSamples));
      const chunks = _.chunk(
        _.range(settings.nSamples),
        Math.floor(settings.nSamples / settings.nCenters)
      );
      if (chunks.length > settings.nCenters) {
        // merge remainder with last chunk
        chunks[settings.nCenters - 1] = _.concat(
          chunks[settings.nCenters - 1],
          chunks[settings.nCenters]
        );
      }
      data.value = _.range(blob.value.nCenters).map((index) => {
        const centerDistribution = MultivariateNormal(
          centers[index],
          stdMatrices[index].to2DArray()
        );
        const values = chunks[index].map(() => centerDistribution.sample());
        return {
          name: `Cluster ${index + 1}`,
          data: values,
        };
      });
    };
    return {
      blob,
      generateData,
      options,
      data,
      changeNumberCenters,
    };
  },
});
</script>

